@import url(https://fonts.googleapis.com/css2?family=Marck+Script&family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bad+Script&family=Marck+Script&family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Marck+Script&family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bad+Script&family=Marck+Script&family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.frontpage {
  background-image: url(/Bootcamp/static/media/top.2559d9dc.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  overflow: hidden;
  font-family: "Satisfy", cursive;
}

.brand {
  font-size: 5vh;
}
.text {
  padding-top: 10vh;
  font-size: 5vh;
  color: #ecf1fb;
  text-align: left;
}
.form {
  padding-top: 10vh;
  padding-bottom: 2vh;
  color: #ecf1fb;
  font-size: 1.4rem;
}
.para {
  font-size: 7.5vh;
  color: rgb(102, 226, 154);
}

.home {
  background-image: url("/Bootcamp/static/media/white flame.91601a65.jpg");
  min-height: 100vh;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.rules {
  font-family: "Bad Script", cursive;

  padding-top: 10vh;
  font-size: 1.3rem;
  color: #ecf1fb;
}
.description {
  font-family: "Bad Script", cursive;
  padding-top: 15vh;
  font-size: 1.5rem;
  color: #ecf1fb;
  line-height: 8vh;
}
h2 {
  text-align: center;
}
hr {
  border-bottom: honeydew solid 1px;
}

.create {
  background-image: url("/Bootcamp/static/media/white flame.91601a65.jpg");
  min-height: 100vh;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #ecf1fb;
}
.task-form {
  font-family: "Bad Script", cursive;
  margin-top: 5vh;
  padding-bottom: 2vh;
  font-size: 1.2rem;
}
img {
  max-height: 100%;
  max-width: 100%;
}

.grade {
  background-image: url(data:image/jpeg;base64,);
  min-height: 100vh;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #ecf1fb;
}

.score {
  font-family: "Bad Script", cursive;
  font-size: 1.4rem;
}
.card-title {
  color: black;
  font-family: "Bad Script", cursive;
  font-size: 1.5rem;
  padding: 2vh;
  border-bottom: solid 1px black;
}
.cardbody {
  border-bottom: solid 1px black;
}
.cards {
  padding-bottom: 3vh;
}

